'use client'
import { useState } from 'react';
import Select from './select';

interface FormData {
  id: string;
  phone: string;
}

const initialState: FormData = {
  id: '',
  phone: '',
};

const options = [
  { value: "dni", label: "DNI" },
  { value: "carnet", label: "CE" },
  { value: "pasaporte", label: "Pasaporte" },
];

const ContactForm: React.FC = () => {
  const [formData, setFormData] = useState(initialState);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);

  const handleSelectChange = (value: string) => {
    console.log("Selected:", value);
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = event.target;
    setFormData((prevFormData) => ({ ...prevFormData, [name]: value }));
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setIsSubmitting(true);
    setTimeout(() => {
      setIsSubmitting(false);
      setIsSubmitted(true);
    }, 2000);
  };

  return (
    <div className="max-w-md mx-auto p-4 bg-white rounded-lg shadow-md">
      <p className='bg-gradient-to-r from-teal-400 to-yellow-200'>Seguro Salud Flexible</p>
      <h2 className="text-lg font-bold mb-4">Creado para ti y tu familia</h2>
      <form onSubmit={handleSubmit}>
        <h3 className='mb-4'>Tú eliges cuánto pagar. Ingresa tus datos, cotiza y recibe nuestra asesoría. 100% online.</h3>
        <div>         
          <div className="mb-4">
            <Select options={options} onChange={handleSelectChange}></Select>
            <label className="block text-sm font-medium mb-2" htmlFor="id">
              Nro. de documento
            </label>
            <input
              className="block w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring focus:border-blue-500"
              type="text"
              id="name"
              name="name"
              value={formData.id}
              onChange={handleChange}
            />
          </div>
        </div>
        <div className="mb-4">
          <label className="block text-sm font-medium mb-2" htmlFor="phone">
            Celular
          </label>
          <input
            className="block w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring focus:border-blue-500"
            type="email"
            id="email"
            name="email"
            value={formData.phone}
            onChange={handleChange}
          />
        </div>
        <button
          className={`w-full p-2 bg-black text-white rounded-lg focus:outline-none focus:ring focus:border-blue-500 ${isSubmitting ? 'opacity-50 cursor-not-allowed' : ''
            }`}
          type="submit"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'Submitting...' : 'Cotiza aquí'}
        </button>
        {isSubmitted && (
          <p className="text-green-500 mt-4">Form submitted successfully!</p>
        )}
      </form>
    </div>
  );
};

export default ContactForm;
